.a {
  height: 170vh;
  display: flex;
  align-items: center;
}

.educationDiv{
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 50px;
}

.educationTittle{
  font-size: 50px;
  font-weight: 600;
  margin-left: 175px;
  margin-top: 220px;
}

.educationCaption{
  color: rgb(255, 255, 255);
}

.workingImg{
  width: 600px;
  height: 400px;
  border-radius: 50%;
  margin-left: 10px;
}


.Skills{
  display: flex;
  
 
}

.card{
  margin-left: 18px;
  background-color: rgb(102, 102, 102);
}

.cardTeaxt{
  color: rgb(0, 0, 0);
}

.cardTittle{
  color: rgb(250, 250, 250);
}

.skillTittle{
  margin-top: 80px;
  font-size: 50px;
  font-weight: 600;
  margin-left: 400px;
}

.unlist{
  margin-left: 100px;
}

.a-left {
  flex: 1;
  display: flex;
  position: relative;
  height: 100%;
  
}

.a-right {
  flex: 1;
   
}

.a-card.bg {
  position:relative;
  top: 100px;
  left:80px;
  background-color: rgb(95, 95, 95);
}

.a-card {
  position: absolute;
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  left: 20px;
  top: 50px;
  overflow: hidden;
}

.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.a-award-img {
  width: 120px;
  height: 120px;
  border-radius: 20px;
}

.a-title {
  font-size: 50px;
  font-weight: 600;
 
}

.a-sub {
  margin: 20px 0px;
}

.a-desc{
    font-weight: 300;
}

.certificateDiv{
  display: flex;
  flex-direction:column;
}

.a-award{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-award-texts{
    width: 70%;
}

.a-award-title{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}

@media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}